export default {
    methods: {
        /**
         * 
         * @param {} sName 
         * @param {} sValue 
         */
        updateRadioSel(sName, sValue) {
            this.$store.dispatch('updateRadioSel', {
                modulesIndex: this.modulesIndex,
                sName: sName,
                newValue: sValue
            })
        },
        /**
         * listallValuevalue
         * @param {*} o
         */
        updateText(o) {
            if (!isNaN(o.listIndex)) {
                o.modulesIndex = this.modulesIndex
            }
            this.$store.dispatch('updateText', o)
        },
        /**
        * data.list
        * @param { list } index 
        */
        removeList(iIndex) {
            this.$store.dispatch('removeList', {
                modulesIndex: this.modulesIndex,
                listIndex: iIndex
            })
        },
        /**
         * data.list
         * @param {  } sType 
         */
        addList(sType) {

            if (sType == "imgList") {
                if (sessionStorage.getItem("aPicture"))
                    sessionStorage.removeItem("aPicture")
                let o = {
                    bShowDialog: true,
                    currentPage: 1,
                    pageSize: 12,
                    oneOrMore: "more",
                    bAlbum: true,
                    modulesIndex: this.modulesIndex,
                    maxLength: this.maxLength,
                    residueLength: this.maxLength - this.onlineData.list.length
                }
                this.$store.dispatch('setDialogPicture', o)
            } else {
                let o = {
                    modulesIndex: this.modulesIndex,
                    url: "",
                    urlCatetory: "",
                    urlName: "",
                    desc: ""
                }
                this.$store.dispatch('addList', o)
            }
        },
        /**
         * title allValue
         * @param {  } sName 
         * @param { value } sValue 
         */
        updateTitleText(sName, sValue) {
            let o = {
                modulesIndex: this.modulesIndex,
                dataNext: "allValue",
                attrName: sName,
                newValue: sValue
            }
            this.updateText(o)
        },
    },

}


